:root {
  --white: #fff;
  --yellow: #a89c64;
  --orange: #946838;
  --red: #9b3f27;
  --green: #454226;
  --primary: "Segoe UI";
  --secondary: "Gloria Hallelujah";
}
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Segoe UI", "Helvetica", sans-serif;
  font-family: var(--primary), "Helvetica", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  background: #fff;
  background: var(--white);
  color: #454226;
  color: var(--green);
  min-width: 100%;
}

nav img {
  height: 75px;
  padding-left: 0.5rem;
}

nav h1 {
  font-family: "Gloria Hallelujah", cursive;
  font-family: var(--secondary), cursive;
  font-size: 2.5rem;
  margin: 0 1rem;
  flex: 1 1;
  text-align: left;
  text-shadow: 1px 1px 2px #a89c64;
  text-shadow: 1px 1px 2px var(--yellow);
}

nav ul {
  list-style-type: none;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0;
}

nav li {
  padding: 1rem;
  transition: all 0.15s ease;
  border-radius: 2rem;
  color: #9b3f27;
  color: var(--red);
  font-size: 1.25rem;
}

nav li:hover {
  background: rgba(0, 0, 0, 0.05);
  color: #946838;
  color: var(--orange);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.5);
  cursor: pointer;
}

/*
* Prefixed by https://autoprefixer.github.io
* PostCSS: v7.0.29,
* Autoprefixer: v9.7.6
* Browsers: last 4 version
*/

.App {
  text-align: center;
  background: var(--yellow);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 101vh;
}

.high-score {
  background: var(--red);
  border-radius: 0 0 3rem 0;
  box-shadow: 0 3px 3px var(--green);
  -ms-grid-row-align: start;
  align-self: start;
  padding: 0 2rem;
  font-size: 1.25rem;
}

.game-over {
  color: var(--yellow);
  margin: auto;
  width: 75%;
}

.new-game,
.game-over button {
  background: var(--orange);
  font-family: inherit;
  text-transform: lowercase;
  font-size: 1rem;
  border-radius: 2rem;
  padding: 0.5rem 1rem;
  border: none;
  outline: transparent;
  margin: 1rem;
  transition: all 0.15s ease;
}

.new-game:hover,
.game-over button:hover {
  cursor: pointer;
  box-shadow: 0 2px 2px var(--green);
}

/* -------------------------------------------------
gameboard
------------------------------------------------- */
.gameboard {
  animation: fade 1s;
  background: var(--red);
  width: 65%;
  border-radius: 3rem;
  padding-bottom: 0rem;
  box-shadow: 0 3px 3px var(--green);
}

.gameboard h2 {
  color: var(--yellow);
  font-size: 2rem;
  margin-bottom: 0.5rem;
}

.gameboard span {
  font-family: var(--secondary);
  font-size: 2.5rem;
}

.board {
  display: -ms-grid;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
  padding: 0 2rem 3rem 2rem;
  grid-gap: 1rem;
  gap: 1rem;
}

.card {
  background: rgba(0, 0, 0, 0.2);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  padding: 0.5rem 0;
  transition: transform 0.15s ease;
}

.card:hover {
  cursor: pointer;
  transform: scale(1.1);
  background: rgba(0, 0, 0, 0.3);
}

.card p {
  margin: 0;
  color: var(--yellow);
}

/* -------------------------------------------------
instructions
------------------------------------------------- */
.instructions {
  animation: fade 1s;
  background: var(--orange);
  width: calc(40% - 2rem);
  border-radius: 3rem;
  box-shadow: 0 3px 3px var(--green);
  padding: 0.5rem 2rem;
  border: 2px solid var(--red);
}

.instructions h2 {
  text-transform: uppercase;
}

.instructions button {
  text-transform: lowercase;
  background: var(--red);
  font-family: inherit;
  font-size: 1rem;
  border-radius: 2rem;
  padding: 0.5rem 1rem;
  border: none;
  outline: transparent;
  transition: all 0.15s ease;
  margin: 1rem;
}

.instructions button:hover {
  cursor: pointer;
  box-shadow: 0 2px 2px var(--green);
}

.instructions p {
  font-size: 1rem;
}

footer {
  flex: 1 1;
  font-size: 0.75rem;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  padding: 1rem;
}

.victory {
  color: var(--yellow);
  margin: 4rem 0;
  font-size: 2rem;
  font-family: var(--secondary);
  animation: excite 2s infinite alternate;
}

.hide {
  animation: shrink 0.3s ease forwards;
}

/* ------------------------------------------- */
/* animations */
/* ------------------------------------------- */

@keyframes fade {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes shrink {
  0% {
    transform: scale(1);
  }
  15% {
    transform: scale(0.1);
  }
}

@keyframes excite {
  0% {
    transform: scale(1);
    color: var(--white);
  }
  50% {
    color: var(--orange);
  }
  100% {
    -webkit-transform: scale(0.75);
    color: var(--yellow);
    transform: scale(0.75);
  }
}

